import * as S from './PhotoSlider.style'
import { ReactChild, FC } from 'react'
import { Box } from '@mui/material'
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/effect-cube";
import "swiper/css/pagination";
import { EffectCube, Pagination } from "swiper";
import Gallery from '../../img/gallery.png'
const { log } = console

interface PhotoSliderProps {

	children?: ReactChild,

}

const slides = [Gallery, Gallery, Gallery, Gallery,]
const PhotoSlider: FC<PhotoSliderProps> = ({ children }) => {

	return (
		<S.PhotoSlider className="PhotoSlider">

			<Swiper
				effect={"cube"}
				grabCursor={true}
				cubeEffect={{
					shadow: true,
					slideShadows: true,
					shadowOffset: 20,
					shadowScale: 0.94,
				}}
				pagination={true}
				modules={[EffectCube, Pagination]}
				className="mySwiper"
			>
				{slides.map(slide =>
					<SwiperSlide>
						<img src={slide} />
					</SwiperSlide>
				)}
			</Swiper>

		</S.PhotoSlider>
	)
}
export default PhotoSlider